
import Popup from 'vux/src/components/popup/index.vue'
import Range from 'vux/src/components/range/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'TeamReg',
components: {
Popup,
Range,
XHeader
},
data() {
return {
topMenu:[{'memuName':'',menuUrl:'/teamInfo'},{'memuName':'',menuUrl:'/teamOrder'},
{'memuName':'',menuUrl:'/teamCashFlow'},{'memuName':'',menuUrl:'/teamSurplus'},
{'memuName':'',menuUrl:'/teamUser'},{'memuName':'',menuUrl:'/teamReg'}],
index:5,
show:false,
regInfo:{
url:'',
regRakeOff:''
},
rakeOff:'0.0'
};
},
async created() {
this.$vux.loading.show({
text: 'Loading'
});
this.getTeamRegInfo();
this.$vux.loading.hide();
},
methods: {
async getTeamRegInfo() {

let res = await this.$http('/queryPromotionUrl');
this.regInfo = Object.assign(this.regInfo, res.returnMap);
console.log(this.regInfo);
} ,
async updateRegInfo() {

let res = await this.$http('/updateUserRakeOff', {
body: {
rakeOff: this.rakeOff
}
});
if(res.returnCode=='0000'){
this.$vux.toast.show({
text: ``,
type: 'success'
});
}else{
this.$vux.toast.show({
text: res.returnMessage,
type: 'warn'
});
}
}
}
};



